import { createStore } from 'vuex'
import http from '@/plugins/axios'
export default createStore({
  state: {
    me: {}
  },
  getters: {

  },
  mutations: {
    getMe(state, data) {
      state.me = data
    }
  },
  actions: {
    getMe({ commit }) {
      http.get('/api/user/me').then(res => {
        console.log(res.data)
        commit('getMe', res.data) 
      })
    }
  },
  modules: {

  }
})
