import { createRouter, createWebHistory } from 'vue-router'
import Login from '../views/Auth/Login.vue'
import Director from '../views/Director/Director.vue'
import Financier from '../views/Financier/Financier.vue'
import Warhen from '../views/Warhen/Warhen.vue'
import Salesman from '../views/Salesman/Salesman.vue'
import Profile from '../views/profile/profile.vue'
const routes = [
  {
    path: '/',
    name: 'login',
    component: Login
  },
  {
    path: '/director',
    name: 'director',
    component: Director,
    children: [
      {
        path: '/profile',
        name: 'profile_director',
        component: Profile
      }
    ]
  },
  {
    path: '/financier',
    name: 'financier',
    component: Financier,
    children: [
      {
        path: '/profile',
        name: 'profile_financier',
        component: Profile
      }
    ]
  },
  {
    path: '/warhen',
    name: 'warhen',
    component: Warhen,
    children: [
      {
        path: '/profile',
        name: 'profile_warhen',
        component: Profile
      }
    ]
  },
  {
    path: '/salesman',
    name: 'salesman',
    component: Salesman,
    children: [
      {
        path: '/profile',
        name: 'profile_salesman',
        component: Profile
      }
    ]
  },
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

router.beforeEach((to, from, next) => {
  const token = sessionStorage.getItem('token')
  const role = sessionStorage.getItem('role')
  const name = to.name === 'login'
  if (!token && !name) {
    return next({ name: 'login' })
  } else {
    if (token && name) {
      if (role === 'warehouseman') {
        return next({ name: 'warhen' })
      } else if (role === 'financier') {
        return next({ name: 'financier' })
      } else if (role === 'salesman') {
        return next({ name: 'salesman' })
      } else if (role === 'director') {
        return next({ name: 'director' })
      } 
    } else {
      next()
    }
  }
})

export default router;
